body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: ZCOOL XiaoWei;
  color: white; 

}

button{
  cursor: pointer;
}

body{
  background-image: url("https://i.pinimg.com/736x/9c/43/fe/9c43fe86ddb72f4ef396427637aadc54--solid-background-wallpaper-iphone--wallpaper.jpg");
  background-position: center;
  background-size: cover;
}

  /* font-size: calc(10px + 2vmin); */



.contact-page h1{
    font-family: ZCOOL XiaoWei;
    color: white;
    letter-spacing: 2px;
    font-size: 40px;
    text-align: center;
}
.contacts-list{
    list-style: none;
    padding: 0;
    margin: 0;
    }
.contact-preview{
    display: flex;
    align-items: center;
    background-image: lightblue;
    max-width: 400px;
    padding: 0 20px;
    border-top: 1px solid lightgrey;
    border-bottom:  1px solid lightgrey;;
    cursor: pointer;
    height: 40px;
    color: white;
    font-weight: 700;
    letter-spacing: 1px;
    font-family: ZCOOL XiaoWei;
}


.contact-preview img{
    border-radius: 125px;
    height: 35px;
}

.new-contact{
    display: flex;
    justify-content: flex-end;
    margin: 48vh 0 0 50vh;
    cursor: pointer;
    padding: 2px;
    z-index: 1000;
    position: fixed;

}

.filter{
    margin: 0;
    
}

.filter input{
    max-width: 500px;
    height: 30px;
    background-color:lightgray;
    border-bottom: 1px solid lightgray;
    min-width: 100%;

}

.filter input ::-webkit-input-placeholder{
    color: black;
}

.filter input ::-ms-input-placeholder{
    color: black;
}

.filter input ::placeholder{
    color: black;
}

@media (min-width: 420px) {
  
  .contact-page{
      display: flex;
      flex-direction: column;
      align-items: center;
  }

  .contacts-list{
    width: 400px
}
    .contact-preview{
      border: 2px solid white;
      padding: 5px ;
  }

  .filter input{
      min-width: 400px;
  }

  .new-contact{
    margin: -12vh 0 0 20vh; 
  }   

}

nav{
    width: 100%;
}
.nav-container ul{
    display: flex;
    list-style: none;
    justify-content:space-evenly;
    background-color: #3a2b2b;
    margin-top: 0.5px;
    padding:  10px ;
}
.nav-container img{
    height: 50px;
    margin: 0  20px 10px 20px;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container h4{
    font-size: 40px;
    text-transform: capitalize;
    margin: 10px;
    color:white;
    font-family: ZCOOL XiaoWei;

}
.container  .home-coins-container , .home-rate-container{
    color: white;
    padding: 5px;
    margin: 10px 0 0 0;
    display: flex;
    flex-direction: row;
    width: 200px;
    font-size: 20px;

}
.container img{
    display: inline-block;
    height: 50px;
    margin-right: 10px;
}

.move-preview{
 padding: 10px  25px;
 list-style: none;
}

.move-preview p{
    margin: 2px;
    text-align: left;
    margin-left: -20px
}

.moves-container{
    color:white;
    text-align: center;
    font-size: 18px;
}

.moves-list{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1px;
}

.moves-home-title{
    font-size: 20px;
    margin: 1px;
    text-decoration: underline;
}

.static-container{
    font-family: ZCOOL XiaoWei;
    color: white;
    letter-spacing: 1px;
    text-align: center;
}

.static-container h4{
    font-size: 40px;
    margin: 30px;
}
.chart{
    width: 80%;
    margin: 50px auto;
}

.chart-subtitle{
  font-size: 20px;
  padding: 0 30px;
  text-decoration: underline;
}

.chart-description{
    padding: 0 30px;
    text-align: left;
}
@media (min-width: 420px) {
.chart-description{
    text-align: center;
}
}


.contact-details{
    display: flex;
    flex-direction: column;
    margin-top: -10px;
    color: white;
    font-family: ZCOOL XiaoWei;

    }

.contact-details img{
    width: 200px;
    height: 200px;
    margin: -50px auto 0 auto;
    border-radius: 125px;
}

.contact-name{
    font-size: 30px;
    font-weight: 900;
    margin: 8px;
    text-align: center;
}

.contact-email span,.contact-phone span {
    font-weight: 700;
    padding: 0 10px;
    font-size:20px;
}

.contact-email, .contact-phone{
    margin: 5px;

}

.buttons-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 10px 20px;    
}

.contact-details a{
     text-decoration: none;
     font-family: ZCOOL XiaoWei;
     color: white;
     cursor: pointer;
}


.transfer-container{
    padding: 15px;
}
.transfer-input{
    margin:  10px auto;
}
.transfer-btn{
    width: 100px;
margin:  2px auto;
background: lightgreen;
padding:5px;
border-radius: 25px;
cursor: pointer;
}

@media (min-width: 420px) {
    .contact-details{
     text-align: center;
    }
}
.edit-add-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.inner-edit-nav{
    margin: auto;
    width: 80%;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}



.robot-edit-add{
    width: 200px;
    height: 200px;
    margin: -50px auto 0 auto;
    border-radius: 125px;
}

.back, .remove{
  height: 50px;
  width: 50px;
}
form {
    display: flex;
    flex-direction: column;
    margin: 20px auto;
    background-color: lightgray;
     width: -webkit-fit-content;
     width: -moz-fit-content;
     width: fit-content;
    color: black;
}

form label{
    margin: 20px ;
}
form input{
    margin: 0 20px;
}

.submit-btn{
    width: 100px;
    margin:  20px auto;
    background: lightgreen;
    padding:10px;
    border-radius: 25px;
}

@media (min-width: 420px) {
  
}
.sign-up-container{
    display: flex;
    flex-direction: column;
    margin: auto;
    font-size: 20px;
    text-align: center;
    color: white;
    font-weight: 700;
    letter-spacing: 1px;
}

.sign-up-container img{
    
    height: 200px;
    width: 200px;
    margin: 50px auto 20px auto;
}

.sign-up-input{
    padding: 4px;
    margin:10px  auto;
    width: 300px;
}

.sign-up-container button{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 5px 10px;
    margin: auto;
    background-color: orange;
    border: 2px inset cornsilk;
    border-radius: 125px;
    font-family: ZCOOL XiaoWei;
    font-size: 18px;

}
